:root{
    --black: #1c1c1c;
    --white: #fdfdfd;
}
.header{
    padding: 1em;
    background-color: var(--black);
}
.margin{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-left: 20%;
    margin-right: 20%;
}

.logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 40%;
}
.logo i{
    font-size: 30px;
    color: var(--white);
}
.logo h1{
    color: var(--white);
    margin-left: 2%;
}
.nav ul{
    display: flex;
    flex-direction: row;
}
.nav ul>li{
    list-style-type: none;
}
.nav a{
    color: var(--white);
    list-style: none;
    font-weight: bold;
    font-size: 18px;
    text-decoration: none;
    padding-left: 2em;
    transition: 0.3s;
}

.nav a:hover{
    color: wheat;
    transition: 0.3s;
}

/* HOME Styling */

.home{
    margin-top: 4%;
    margin-bottom: 4%;
    margin-left: 25%;
    margin-right: 25%;
}

.content{
    background-color: var(--white);
    padding: 2em;
    border-radius: 10px;
}

.content hr{
    width: 10%;
    margin-top: 2%;
    margin-bottom: 2%;
    border: 1px solid wheat;
}

.content p{
    line-height: 20px;
}

.author a{
    color: rgb(37, 122, 193);
    text-decoration: none;
    transition: 0.7s;
}

.author a:hover{
    color: var(--black);
    transition: 0.7s;
}

/* About Styling */

.about{
    margin-top: 4%;
    margin-bottom: 4%;
    margin-left: 25%;
    margin-right: 25%;
}

/* Projects Styling */

.projects{
    margin-top: 4%;
    margin-bottom: 4%;
    margin-left: 25%;
    margin-right: 25%;
}

/* Contact Styling */

.contact{
    margin-top: 4%;
    margin-bottom: 4%;
    margin-left: 25%;
    margin-right: 25%;
}
